



















export default {
	data() {
		return {
			SessionKey: '',
			OpenId: '',
			nickName: null,
			avatarUrl: null,
			isCanUse: uni.getStorageSync('isCanUse')||true,//默认为true
			needAllow:true //是否需要授权
		};
	},
	methods:{
		//注册
		register(){
			uni.switchTab({  
			    url: '/pages/login/register'  
			});  
		},
		async saveCode() {
		    let _this = this;
		    uni.showLoading({
		        title: '登录中...'
		    });
		   // 1.wx获取登录用户code
		    await uni.login({
		        provider: 'weixin',
		        success: function(loginRes) {
		            let code = loginRes.code;
					//存储登录code
					_this.mPost('/auth/wxLogin',code,"https://dy.gantangerbus.com/da"
					).then(res=>{
						if(res.data){
							let data = res.data;
							if(data.isRegister){
								let saveData = data;
								_this.needAllow=false;
								// 	储存用户信息
								// saveData.avatarUrl=data.headAddress||"";
								uni.setStorageSync('userData', JSON.stringify(saveData.userInfo));
								uni.setStorageSync('phone',  saveData.userInfo.mobile);
								uni.setStorageSync('Authorization', saveData.authToken);
								uni.showToast({
									title: "授权成功",
									icon: "none"
								});
								setTimeout(()=>{
									uni.switchTab({  
										url: '/pages/tabBar/home'  
									});
								},1300)
							}
						}
						uni.hideLoading();
					}).catch(err=>{
						uni.hideLoading();
					});
		        },
		    });
		},
		//第一授权获取用户信息===》按钮触发
		
		getuserinfo:function(res){
			if(!uni.getStorageSync('Authorization')){
				this.saveCode();
			}
			let _this = this;
			if (!res.detail.iv) {
				
				uni.showToast({
					title: "您取消了授权,登录失败",
					icon: "none"
				});
				return false;
			}
			uni.showLoading({});
			uni.getUserInfo({
		        provider: 'weixin',
		        success: function(infoRes) {
					// 	储存用户信息
					uni.setStorageSync('userData', JSON.stringify(infoRes.userInfo));
					uni.showLoading({});
					_this.mPost('/auth/wxUserInfo',infoRes.userInfo,"https://dy.gantangerbus.com/da").then(res=>{
						uni.navigateTo({  
							url: '/pages/login/register'  
						}); 
						uni.hideLoading();
					}).catch(err=>{
						uni.hideLoading();
					});
					
		        },
		        fail(res) {
					uni.hideLoading();
				}
		    });
		}
	},
	onLoad(){
		this.saveCode()
	}
}
